    constructor(
        private _amigos: ConexionService,
        private Userdata: ConexionService,){

        }
         
    

    ngOnInit(){
        this.dataUser()
        this.amigoss()
    }

    dataUser(){
        this.Userdata.User().subscribe(data => {
        console.log(data)
        this.DataUser = data;
    })}
   
    amigoss(){
        this._amigos.Amigos().subscribe(data => { 
        this.exampleData = data;
        console.log(this.exampleData)
    })}
    
