import { Component,EventEmitter,Input,OnInit, Output,TemplateRef } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ConexionService } from 'app/servicios/conexion.service';
import { ToastrService } from 'ngx-toastr';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';






@Component({
    moduleId: module.id,
    selector: 'maps-cmp',
    templateUrl: 'maps.component.html'
})

export class MapsComponent implements OnInit {

    singleModel = '1';
    hide = true;
    

    modalRef?: BsModalRef;
    message?: string;
  
    IDUser!: string[]

   send: any
   options: FormGroup;
    exampleData:any[]
     
    enviar: FormGroup;
    
    hideRequiredControl = new FormControl(false);
    
    dasable:boolean = true;
    
    constructor(
        private _pagar: ConexionService,
        private _amigos: ConexionService,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private modalService: BsModalService
        
         
        ) 
    {
       

        
      
            
        
        this.enviar = new FormGroup({
            
            ClavePago: new  FormControl("",Validators.required),
        }
            ); 
    }

     
    enviarr = this.formBuilder.group({
        hideRequired: this.hideRequiredControl,
        IDUser1:['',Validators.required],
        Id:[''],
        monto:['',Validators.required],
    });

    

     ngOnInit(): void { 
        this.amigoss() 
        
}

    amigoss(){
        this._amigos.Amigos().subscribe(data => { 
        this.exampleData = data;
        this.enviarr.get('Id')?.valueChanges.subscribe((data:string)=>{
        let id = this.exampleData.map(IDUser => IDUser.IDUser1 = data);
        this.enviarr.controls['IDUser1'].setValue(id[0])})
    })}

      
       
       

    confir(msj:string){
        this._pagar.pagar(this.send).subscribe(res =>{
            this.toastr.success('<span data-notify="icon" class="nc-icon nc-bell-55"></span>'+"enviaste: "+res['enviado']+"$",msj,
            {
                timeOut: 4000,
                closeButton: true,
                messageClass:"toast-message",
                onActivateTick: false,
                progressBar:true,
                enableHtml: true,
                toastClass: "alert alert-info alert-with-icon",
                positionClass: 'toast-top-right',
                
            }); 
    })
    }
   


    pagar(){
        this.message = 'Confirmed!';
        this.modalRef?.hide();
        
        this.send = {
            "id":this.enviarr.value.IDUser1,
            "monto":this.enviarr.value.monto,
            "add":this.enviarr.value.hideRequired,
            "clavepago":this.enviar.value.ClavePago
        }
        console.log(this.send)
        this._pagar.pagar(this.send).subscribe(res =>{
            this.toastr.success('<span data-notify="icon" class="nc-icon nc-bell-55"></span>'+"enviaste: "+res['enviado']+"$",res['info'],
            {
                timeOut: 4000,
                closeButton: true,
                messageClass:"toast-message",
                onActivateTick: false,
                progressBar:true,
                enableHtml: true,
                toastClass: "alert alert-info alert-with-icon",
                positionClass: 'toast-top-right',
                
            }); 
    })
    
    this.enviar.reset()
    
}
  
   
    




openModal(template: TemplateRef<any>) {
    this.modalRef = this.modalService.show(template);
  }

 
 
  decline(): void {
    this.message = 'Declined!';
    this.modalRef?.hide();
    this.enviarr.reset()
    this.enviar.reset()
   
        this.toastr.success('<span data-notify="icon" class="nc-icon nc-bell-55"></span>'+this.message,'Transacion',
        {
            timeOut: 4000,
            closeButton: true,
            messageClass:"toast-message",
            onActivateTick: false,
            progressBar:true,
            enableHtml: true,
            toastClass: "alert alert-info alert-with-icon",
            positionClass: 'toast-top-right',
            
        }); 


  }

  


}


